
import Swal from "sweetalert2";
export default {
  beforeMount() {
    this.loadData();
  },
  data() {
    return {
      // D liu danh sch
      gridData: {
        default: () => []
      },
      // Form chi tit
      detailForm: '',
      // Kha chnh entity
      keyEntity: ''
    }
  },
  methods: {
    // M form sa
    editRow(row) {
      this.$popup.show(
        { component: this.detailForm },
        {
          data: row,
          mode: this.$enums.ModelState.Edit,
          afterSave: this.loadData,
        }
      );
    },
    // Load d liu
    async loadData() {
      var res = await this.api.get();
      this.gridData = this.customData(res.data)
    },
    // Custom li d liu
    customData(listData) {
      return listData;
    },
    /**
     * M form thm
     */
    add() {
      this.$popup.show(
        { component: this.detailForm },
        {
          data: {},
          mode: this.$enums.ModelState.Add,
          afterSave: this.loadData,
        }
      );
    },
    // Xa d liu
    del(ids) {
      this.api
        .delete({
          Condition: {
            [this.keyEntity]: ids,
          },
        })
        .then(() => {
          this.loadData();
        });
    },
    // Xa dng
    deleteRow(row) {
      this.deletes([row[this.keyEntity]])
    },
    // Xa nhiu
    deletes(deleteIds) {
      Swal.fire({
        title: "Xa d liu",
        text: "Bn c mun xa d liu  chn khng?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "ng ",
        cancelButtonText: "Hy",
      }).then((result) => {
        if (result.isConfirmed) {
          this.del(deleteIds);
        }
      });
    }
  },
}